<template>
    <el-row>
        <el-col :span="24">
            <div class="header-wrap">
                <div class="header">
                    <span class="logo" @click="goIndexPage()"></span>
                    <span class="title-header" v-if="title">{{title}}</span>
                    <slot></slot>
                </div>
            </div>
        </el-col>
    </el-row>
</template>
<script>
    export default {
        name: 'com-header',
        data() {
            return {}
        },
        props: {
            title: {
                type: String,
                default: ""
            }
        },
        methods: {
            goIndexPage() {
                this.$router.push('/')
            }
        },
        created() {

        },

    }
</script>

<style lang="less" scoped>
    .header-wrap {
        width: 100%;
        background: #1e2b48;
        height: 70px;
        .header {
            height: 45px;
            width: 1336px;
            min-width: 750px;
            padding: 13px 0;
            margin: 0 auto;
            color: #fff;
        }
    }

    .title-header {
        display: inline-block;
        margin-left: 30px;
        min-width: 100px;
        height: 35px;
        font-size: 22px;
        line-height: 35px;
        vertical-align: top;
    }

    .logo {
        display: inline-block;
        width: 158px;
        height: 35px;
        background: url('images/logo.png') no-repeat 0 center;
        cursor: pointer;
    }
</style>


